import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import VueLazyLoad from 'vue-lazyload'
import fastclick from 'fastclick'
import {getToken} from 'common/js/cache'
import {ORDER_STATUS,DELIVER_STATUS} from 'api/config'
import Alert from 'common/extend/alert'

import 'common/stylus/index.styl'

Vue.use(Alert)

// import VConsole from 'vconsole'
// let vConsole = new VConsole()

// console.log('hello world')

fastclick.attach(document.body)

// Vue.use(VueLazyLoad, {
//   loading: require('common/image/default.png')
// })

Vue.filter('formatMoney', (value) => {
  if(!isFinite(value)){
      return ''
  }
  return value.toFixed(2)
})

Vue.filter('formatOrderStatus', (value) => {
  return ORDER_STATUS[value] || ''
})

Vue.filter('formatDeliverStatus', (value) => {
  return DELIVER_STATUS[value] || ''
})

// 登录校验
router.beforeEach((to, from, next) => {
  const ROUTE_LOGIN = '/login'
  const ROUTE_INDEX = '/index'
  let isLogin = Boolean(getToken())
  if (isLogin) {//如果登录信息存在
    if (to.matched.length) {//路由如果存在
      next()
    } else {//路由信息如果不存在，跳转到首页
      // 这里需要加上是否是首页的判断
      // 如果是index，直接next()，否则跳到index
      // 如果不这样，在Mac上面会报Maximum call stack size的错误
      if (to.path !== ROUTE_INDEX) {
        next(ROUTE_INDEX)
      } else {
        next()
      }
    }
  } else {//登录信息如果不存在，直接跳转到登录页面
    if (to.path !== ROUTE_LOGIN) {
      next(ROUTE_LOGIN)
    } else {
      next()
    }
  }
})

export default new Vue({
  el: '#app',
  render: h => h(App),
  store,
  router
})


