import { getAutoLoginFlag, getProduct, getShoppingCartItems, getAddressDetail, getHistory, getStore} from 'common/js/cache'

const state = {
    isLoginPage: true,
    isAutoLogin: getAutoLoginFlag(),
    currentProduct: getProduct(),
    shoppingCartItems: getShoppingCartItems(),
    addressInfo: getAddressDetail(),
    searchHistory: getHistory(),
    storeList: getStore()
}
export default state


