import $alert from 'base/alert/alert.vue'

const Alert = {
    install(Vue, options = {
        confirmBtnText: '确定',
        message: '错误'
    }) {
        if (document.getElementsByClassName('alert-wrapper').length) {
            return
        }
        let alertTpl = Vue.extend($alert) // 创建vue构造器
        // el：提供一个在页面上已存在的DOM元素作为Vue实例的挂载目标。可以是css选择器，也可以是HTMLElement实例。
        // 在实例挂载之后，可以通过$vm.$el访问。
        // 如果这个选项在实例化时有用到，实例将立即进入编译过程。否则，需要显示调用vm.$mount()手动开启编译(如下)
        // 提供的元素只能作为挂载点。所有的挂载元素会被vue生成的dom替换。因此不能挂载在顶级元素(html, body)上
        let $vm = new alertTpl() // 实例化vue实例
        // 此处使用$mount来手动开启编译。用$el来访问元素，并插入到body中
        let tpl = $vm.$mount().$el
        document.body.appendChild(tpl)

        Vue.prototype.$alert = { // 在Vue的原型上添加实例方法，以全局调用
            show(options) {
                Object.assign($vm, options) // 合并参数与实例
                $vm.showFlag = true // 显示toast
            }
        }
    }
}

export default Alert


