import storage from 'good-storage'

// 是否是自动登录
const AUTOLOGIN_KEY = '__AUTOLOGIN__'
// 登录帐号密码
const USERINFO_KEY = '__USERINFO__'
// 当前选中的商品
const PRODUCT_KEY = '__PRODUCT__'
// 库存列表
const STORE_KEY = '__STORE__'
// 购物车
const SHOPPINGCART_KEY = '__SHOPPINGCART__'
// 地址
const ADDRESSINFO_KEY = '__ADDRESSINFO__'
// 登录TOKEN
const TOKEN_KEY = '__TOKEN__'
// 搜索历史记录
const SEARCH_HISTORY_KEY = '__SEARCH_HISTORY__'
// 搜索历史记录最大长度
const MAX_LENGTH = 10

// 登录页面flag
export function setAutoLoginFlag(flag) {
    storage.set(AUTOLOGIN_KEY, flag)
}
export function getAutoLoginFlag() {
    return storage.get(AUTOLOGIN_KEY, false)
}

// 用户信息
export function getUserInfo(){
    return storage.get(USERINFO_KEY, '')
}
export function setUserInfo(obj){
    storage.set(USERINFO_KEY, obj)
}
export function removeUserInfo(){
    storage.remove(USERINFO_KEY)
}

// 当前商品
export function getProduct(){
    return storage.get(PRODUCT_KEY, {})
}
export function setProduct(obj){
    storage.set(PRODUCT_KEY, obj)
    return obj
}

// 当前库存列表
export function getStore(){
    return storage.get(STORE_KEY, {})
}
export function setStore(obj){
    storage.set(STORE_KEY, obj)
    return obj
}

// 当前购物车的商品
export function getShoppingCartItems() {
    return storage.get(SHOPPINGCART_KEY, {})
}
export function setShoppingCartItems(obj) {
    storage.set(SHOPPINGCART_KEY, obj)
    return obj
}

//收货人信息
export function getAddressDetail() {
    return storage.get(ADDRESSINFO_KEY, {})
}
export function setAddressDetail(obj) {
    storage.set(ADDRESSINFO_KEY, obj)
    return obj
}

// 传参的token
export function getToken() {
    return storage.get(TOKEN_KEY, '')
}
export function setToken(token) {
    storage.set(TOKEN_KEY, token)
    return token
}
export function removeToken(){
    storage.remove(TOKEN_KEY)
}

// 搜索历史记录
export function getHistory() {
    return storage.get(SEARCH_HISTORY_KEY, [])
}
export function clearHistory() {
    storage.remove(SEARCH_HISTORY_KEY)
    return []
}
export function setHistory(keyword) {
    keyword = (keyword || '').trim()
    let list = getHistory()
    if(keyword !== ''){
        let index = list.indexOf(keyword)
        if(index !== -1){
            list = list.slice(0, index).concat(list.slice(index + 1))
        }
        list.unshift(keyword)
        if(list.length > MAX_LENGTH){
            list.pop()
        }
        storage.set(SEARCH_HISTORY_KEY, list)
    }
    return list
}


